/** Only use this if using Light DOM but want scoped styles */
const styles = `
    data-list {
        --base-margin: var(--base-margin, 1rem);
        --list-style: disc; /* Default list style type */
        --nested-indent: 40px; /* Default nested list indent - all browsers use 40px by default */
    }
    
    data-list ul {
        list-style: var(--list-style);
    }
    
    /* Nested list indentation */
    data-list ul ul {
        padding-inline-start: var(--nested-indent);
        margin-inline-start: 0;
    }
    
    /* No list bullet on an li containing a nested lists unless the key text is shown */
    data-list .nested-container {
        list-style: none;
    }

    /* #region ---- Checkbox list styles ---- */
    data-list .checkbox-list {
        list-style: none;
        padding-inline-start: 0;
    }

    /* Nested checkbox list indentation */
    data-list .checkbox-list ul {
        padding-inline-start: 1.5em;
        margin-inline-start: 0;
        width: 100%;
        pointer-events: auto; /* Ensure nested lists can receive clicks */
    }

    data-list .checkbox-item {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        padding-block-start: 0.25em;
        padding-block-end: 0.25em;
    }

    data-list .checkbox-item > input[type="checkbox"] {
        flex-shrink: 0;
        min-width: 1.5em;
        z-index: 2; /* Ensure checkbox is above other elements */
    }

    data-list .checkbox-item > .checkbox-label {
        flex: 1;
        cursor: pointer;
        user-select: none;
        margin-left: 0;
        padding-left: 0;
        z-index: 1;
    }

    /* Ensure nested lists don't interfere with label clicks */
    data-list .checkbox-item > .checkbox-label > * {
        pointer-events: none; /* Prevent child elements from intercepting clicks */
    }

    data-list .checkbox-item > ul {
        pointer-events: auto; /* Re-enable pointer events for nested lists */
        z-index: 3; /* Ensure nested lists are above labels */
    }

    /* Focus styles for accessibility */
    data-list .checkbox-item > input[type="checkbox"]:focus {
        outline: 2px solid hsl(220, 90%, 50%);
        outline-offset: 2px;
    }
    data-list .checkbox-item > .checkbox-label:focus {
        outline: 2px solid hsl(220, 90%, 50%);
        outline-offset: 2px;
    }
    /* #endregion ---- Checkbox list styles ---- */
`

/** Namespace
 * @namespace Live
 */

/**
 * @class
 * @augments TiBaseComponent
 * @description Define a new zero dependency custom web component ECMA module that can be used as an HTML tag
 *
 * @element data-list
 * @memberOf Live

 * METHODS FROM BASE: (see TiBaseComponent)
 * STANDARD METHODS:
  * @function attributeChangedCallback Called when an attribute is added, removed, updated or replaced
  * @function connectedCallback Called when the element is added to a document
  * @function constructor Construct the component
  * @function disconnectedCallback Called when the element is removed from a document

 * OTHER METHODS:
  * @function entry Dynamically ament/add a single entry. The list is rebuilt.

 * CUSTOM EVENTS:
  * "data-list:connected" - When an instance of the component is attached to the DOM. `evt.details` contains the details of the element.
  * "data-list:ready" - Alias for connected. The instance can handle property & attribute changes
  * "data-list:disconnected" - When an instance of the component is removed from the DOM. `evt.details` contains the details of the element.
  * "data-list:attribChanged" - When a watched attribute changes. `evt.details.data` contains the details of the change.
  * NOTE that listeners can be attached either to the `document` or to the specific element instance.

 * Standard watched attributes (common across all my components):
  * @property {string|boolean} inherit-style - Optional. Load external styles into component (only useful if using template). If present but empty, will default to './index.css'. Optionally give a URL to load.
  * @property {string} name - Optional. HTML name attribute. Included in output _meta prop.

 * Other watched attributes:
  * @property {string} type - 💫 Optional. The type of list to use, ul or ol. Default is ul.
  * @property {string} listvar - 💫 Optional. The global variable name to use for the list data. If not set, set the data property directly from JS.
  * @property {string} keyvalueseparator - Optional. The separator to use between key and value in the list items when input is an object. Default is ' :: '. Set to "NULL" to disable key display.
  * @property {string} liststyle - 💫 Optional. The style type to use for the list. Default is `disc` for `ul` and `decimal` for `ol`. May contain any valid CSS list-style string value.

 * PROPS FROM BASE: (see TiBaseComponent)
 * OTHER STANDARD PROPS:
  * @property {string} componentVersion Static. The component version string (date updated). Also has a getter that returns component and base version strings.

 * Other props:
  * By default, all attributes are also created as properties
  * @property {object} data - 💫 The data to use for the list. Either set directly or via the `listvar` attribute. If an object, the key/value separator is used to separate the key and value in the list items.

 NB: properties marked with 💫 are dynamic and have getters/setters. They will cause the list to rebuild.

 * @slot Container contents

 * @example
  * <data-list id="myComponent" type="ol" inherit-style="./myComponent.css"></data-list>

 * See https://github.com/runem/web-component-analyzer?tab=readme-ov-file#-how-to-document-your-components-using-jsdoc
 */
class DataList extends TiBaseComponent {
    /** Component version */
    static componentVersion = '2025-06-03'

    /** The top-level list element - this is created in the shadow DOM */
    #list = null
    /** The list style type to use for the list - this is set as a CSS variable */
    #listStyle = null
    /** The data to use for the list - this is set via the `data` property */
    #entries = null
    /** The type of list to use, ul or ol @type {"ol"|"ul"} */
    #type = 'ul'
    /** Whether the list is currently in checkbox mode */
    #isCheckboxMode = false

    // Optional global variable name to use for the list data
    listvar = null
    // The separator to use between key and value in the list items
    keyvalueseparator = ' :: '

    /** Makes HTML attribute change watched
     * @returns {Array<string>} List of all of the html attribs (props) listened to
     */
    static get observedAttributes() {
        return [
            // Standard watched attributes:
            'inherit-style', 'name',
            // Other watched attributes:
            'keyvalueseparator', 'liststyle', 'listvar', 'type', 'topic',
        ]
    }

    /** NB: Attributes not available here - use connectedCallback to reference */
    constructor() {
        super()
        // Only attach the shadow dom if code and style isolation is needed - comment out if shadow dom not required
        // if (template && template.content) this._construct(template.content.cloneNode(true))

        // Otherwise, if component styles are needed, use the following instead:
        this.prependStylesheet(styles, 0)
    }

    /** Runs when an instance is added to the DOM
     * Runs AFTER the initial attributeChangedCallback's
     * @private
     */
    connectedCallback() {
        // console.info(`[${this.localName}] ${this.id} connectedCallback (${this.tagName})`)
        this._connect() // Keep at start.

        // If a listvar is specified, make sure it exists globally, if it does, use it to set the data property
        if ( this.listvar && window[this.listvar] ) {
            if ( window[this.listvar] ) {
                this.data = window[this.listvar]
                // window.dataList.instances[this.id] = window[newVal]
                // console.log('>> listvar change >>', this.id, this.#entries, window.dataList.instances[this.id])
                //window[newVal] = new Proxy()
            } else {
                console.warn(`[${this.localName}] ${this.id} window.${this.listvar} does not exist, ignoring.`)
            }

            return
        }

        this._ready() // Keep at end. Let everyone know that a new instance of the component has been connected & is ready
    }

    /** Runs when an instance is removed from the DOM
     * @private
     */
    disconnectedCallback() {
        this._disconnect() // Keep at end.
    }

    /** Runs when an observed attribute changes - Note: values are always strings
     * NOTE: On initial startup, this is called for each watched attrib set in HTML.
     *       and BEFORE connectedCallback is called.
     * @param {string} attrib Name of watched attribute that has changed
     * @param {string} oldVal The previous attribute value
     * @param {string} newVal The new attribute value
     * @private
     */
    attributeChangedCallback(attrib, oldVal, newVal) {
        // console.info(`[${this.localName}] ${this.id} attributeChangedCallback: ${attrib} changed from "${oldVal}" to "${newVal}"`)
        /** Optionally ignore attrib changes until instance is fully connected
         * Otherwise this can fire BEFORE everthing is fully connected.
         */
        // if (!this.connected) return

        // Don't bother if the new value same as old
        if ( oldVal === newVal ) return
        // Create a property from the value - WARN: Be careful with name clashes
        this[attrib] = newVal

        // Add other dynamic attribute processing here.
        // If attribute processing doesn't need to be dynamic, process in connectedCallback as that happens earlier in the lifecycle

        // Keep at end. Let everyone know that an attribute has changed for this instance of the component
        this._event('attribChanged', { attribute: attrib, newVal: newVal, oldVal: oldVal, })
    }

    // Getter/setter for the `type` public property - maps to this.#type
    set type(val) {
        if (!['ul', 'ol'].includes(val)) {
            console.warn(`[${this.localName}] ${this.id} Invalid type "${val}" specified, defaulting to "ul".`)
            val = 'ul'
        }

        this.#type = val

        this._updateListAttributes(this.#list, this.#type)
    }
    get type() {
        return this.#type
    }

    // Getter/setter for the `listStyle` public property - maps to this.#listStyle
    set liststyle(val) {
        if (val === 'checkbox') {
            // Special handling for checkbox list style
            this.#listStyle = 'none'
            this.#isCheckboxMode = true

            // Wait for the list to be built if data is being set
            if (this.#list) {
                console.info(`[${this.localName}] ${this.id} List exists. Replacing list with checkboxes`)
                this.replaceWithCheckboxes()
            } else {
                console.info(`[${this.localName}] ${this.id} List does not exist yet. Waiting for data to be set before replacing with checkboxes.`)
                // If list doesn't exist yet, set up a one-time listener for when data is set
                const handleDataChange = () => {
                    console.info(`[${this.localName}] ${this.id} Data changed, replacing list with checkboxes`)
                    this.replaceWithCheckboxes()
                    this.removeEventListener(`${this.localName}:dataChanged`, handleDataChange)
                }
                this.addEventListener(`${this.localName}:dataChanged`, handleDataChange)
            }
        } else {
            this.#listStyle = val
            this.#isCheckboxMode = false
            this._updateListAttributes(this.#list, this.#type)
        }
    }
    get liststyle() {
        return this.#isCheckboxMode ? 'checkbox' : this.#listStyle
    }

    // Getter/setter for the `data` public property - maps to this.#entries
    set data(val) {
        this.#entries = val

        // If the list already exists, delete it
        if (this.#list) this.#list.remove()

        // Dynamically build the content of the ul/ol
        // Object.keys(this.#entries).forEach( this.buildListItem.bind(this))
        this._buildList(this.#entries, this, this.#type, 1)

        this.#list = this.querySelector('ul')
        this._updateListAttributes(this.#list, this.#type)

        // Issue a custom event to notify that the data has changed
        this._event('dataChanged', this.#entries)
    }
    get data() {
        return this.#entries
    }

    /** Dynamically change/add list entries - rebuilds the list DOM element
     * @param {string|number} key Object key or array index to change/add
     * @param {string} val Updates/new list text (can be HTML)
     * @example
     * // Add a new entry to the list
     * dataList.entry('newKey', 'New list item text')
     * // Update an existing entry in the list
     * dataList.entry('existingKey', 'Updated list item text')
     */
    entry(key, val) {
        this.#entries[key] = val
        this.data = this.#entries
    }

    /** Replaces ul list item bullets with checkbox input fields
     * @param {HTMLUListElement|null} listElement The ul element to modify. If null, uses the main list
     * @param {boolean} checked Initial checked state for all checkboxes
     * @param {string} name Name attribute for checkbox group (optional)
     * @returns {Array<HTMLInputElement>} Array of created checkbox elements
     * @example
     * // Replace bullets with unchecked checkboxes
     * dataList.replaceWithCheckboxes()
     * // Replace bullets with checked checkboxes and group name
     * dataList.replaceWithCheckboxes(null, true, 'todo-items')
     */
    replaceWithCheckboxes(listElement = null, checked = false, name = '') {
        const targetList = listElement ?? this.#list

        if (!targetList || targetList.tagName.toLowerCase() !== 'ul') {
            console.warn(`[${this.localName}] ${this.id} replaceWithCheckboxes: No valid ul element found`)
            return []
        }

        const checkboxes = []
        const listItems = targetList.querySelectorAll(':scope > li')

        listItems.forEach((li, index) => {
            // Skip if this li already has a checkbox
            if (li.querySelector('input[type="checkbox"]')) {
                return
            }

            // Extract only direct child nested lists (not all descendants)
            const directNestedLists = Array.from(li.children).filter(child =>
                child.tagName.toLowerCase() === 'ul' || child.tagName.toLowerCase() === 'ol'
            )

            // Temporarily remove direct nested lists to get only the direct text content
            directNestedLists.forEach(nestedList => nestedList.remove())

            // Get the direct text content of this list item (without nested lists)
            const directTextContent = li.textContent?.trim()
            const directHTML = li.innerHTML.trim()

            // Skip checkbox creation if there's no meaningful text content
            // (i.e., the li only contains nested lists)
            if (!directTextContent || directHTML === '') {
                // Put the nested lists back and skip checkbox creation
                directNestedLists.forEach(nestedList => {
                    li.appendChild(nestedList)
                    // Still recursively apply checkbox conversion to nested lists if they are ul elements
                    if (nestedList.tagName.toLowerCase() === 'ul') {
                        this.replaceWithCheckboxes(nestedList, checked, name)
                    }
                })
                // Add a class to indicate this is a container item without checkbox
                li.classList.add('nested-container')
                return
            }

            // Create checkbox input element
            const checkbox = document.createElement('input')
            checkbox.type = 'checkbox'
            checkbox.checked = checked
            checkbox.id = `${this.id}-checkbox-${index}`
            checkbox.setAttribute('aria-label', `Checkbox for ${directTextContent}`)

            if (name) {
                checkbox.name = name
            }

            // Add value attribute based on direct text content
            if (directTextContent) {
                checkbox.value = directTextContent
            }

            // Create label wrapper for accessibility
            const label = document.createElement('label')
            label.htmlFor = checkbox.id
            label.className = 'checkbox-label'

            // Only put the direct content (not nested lists) in the label
            label.innerHTML = directHTML

            // Clear li and add checkbox + label
            li.innerHTML = ''
            li.appendChild(checkbox)
            li.appendChild(label)

            // Add nested lists back after the label (not inside it)
            directNestedLists.forEach(nestedList => {
                li.appendChild(nestedList)
                // Recursively apply checkbox conversion to nested lists if they are ul elements
                if (nestedList.tagName.toLowerCase() === 'ul') {
                    this.replaceWithCheckboxes(nestedList, checked, name)
                }
            })

            // Add CSS class for styling
            li.classList.add('checkbox-item')

            // Add event listener for checkbox changes
            checkbox.addEventListener('change', (e) => {
                const target = /** @type {HTMLInputElement} */ (e.target)
                this._event('checkboxChanged', {
                    checkbox: target,
                    checked: target.checked,
                    value: target.value,
                    index: index,
                    listItem: li,
                })
            })

            checkboxes.push(checkbox)
        })

        // Update list style to remove default bullets
        targetList.style.setProperty('--list-style', 'none')
        targetList.classList.add('checkbox-list')

        // Update checkbox mode state
        this.#isCheckboxMode = true

        // Issue custom event to notify checkboxes have been added
        this._event('checkboxesCreated', {
            list: targetList,
            checkboxes: checkboxes,
            count: checkboxes.length,
        })

        return checkboxes
    }

    /** Removes checkboxes and restores original list bullets
     * @param {HTMLUListElement|null} listElement The ul element to restore. If null, uses the main list
     * @returns {number} Number of checkboxes removed
     * @example
     * // Remove all checkboxes and restore bullets
     * dataList.removeCheckboxes()
     */
    removeCheckboxes(listElement = null) {
        const targetList = listElement || this.#list

        if (!targetList) {
            console.warn(`[${this.localName}] ${this.id} removeCheckboxes: No list element found`)
            return 0
        }

        // First, recursively remove checkboxes from nested lists
        const nestedLists = targetList.querySelectorAll('ul.checkbox-list')
        nestedLists.forEach(nestedList => {
            this.removeCheckboxes(nestedList)
        })

        const checkboxItems = targetList.querySelectorAll(':scope > .checkbox-item')
        const nestedContainers = targetList.querySelectorAll(':scope > .nested-container')
        let removedCount = 0

        checkboxItems.forEach((li) => {
            const checkbox = li.querySelector('input[type="checkbox"]')
            const label = li.querySelector('.checkbox-label')

            if (checkbox && label) {
                // Get only direct child nested lists (not all descendants)
                const directNestedLists = Array.from(li.children).filter(child =>
                    (child.tagName.toLowerCase() === 'ul' || child.tagName.toLowerCase() === 'ol') &&
                    child !== checkbox && child !== label
                )

                // Start with the label content
                let restoredContent = label.innerHTML

                // If there are direct nested lists, append them to the restored content
                if (directNestedLists.length > 0) {
                    directNestedLists.forEach(nestedList => {
                        restoredContent += nestedList.outerHTML
                    })
                }

                // Restore original content
                li.innerHTML = restoredContent
                li.classList.remove('checkbox-item')
                removedCount++
            }
        })

        // Also restore nested containers (items that only had nested lists)
        nestedContainers.forEach((li) => {
            li.classList.remove('nested-container')
        })

        // Restore original list style
        targetList.style.removeProperty('--list-style')
        targetList.classList.remove('checkbox-list')

        // Reset checkbox mode state only if this is the main list
        if (targetList === this.#list) {
            this.#isCheckboxMode = false
        }

        // Update list attributes to restore default styling
        this._updateListAttributes(targetList, this.#type)

        // Issue custom event to notify checkboxes have been removed
        this._event('checkboxesRemoved', {
            list: targetList,
            removedCount: removedCount,
        })

        return removedCount
    }

    /** Gets the current state of all checkboxes in the list
     * @param {HTMLUListElement|null} listElement The ul element to check. If null, uses the main list
     * @returns {Array<{checkbox: HTMLInputElement, checked: boolean, value: string, index: number}>} Array of checkbox states
     * @example
     * // Get all checkbox states
     * const states = dataList.getCheckboxStates()
     * console.log(states) // [{checkbox: input, checked: true, value: 'item1', index: 0}, ...]
     */
    getCheckboxStates(listElement = null) {
        const targetList = listElement || this.#list

        if (!targetList) {
            console.warn(`[${this.localName}] ${this.id} getCheckboxStates: No list element found`)
            return []
        }

        const checkboxes = targetList.querySelectorAll('input[type="checkbox"]')
        return Array.from(checkboxes).map((checkbox, index) => ({
            checkbox: /** @type {HTMLInputElement} */ (checkbox),
            checked: /** @type {HTMLInputElement} */ (checkbox).checked,
            value: /** @type {HTMLInputElement} */ (checkbox).value,
            index: index,
        }))
    }

    /** Sets the checked state for specific checkboxes
     * @param {Array<number>|string} indices Array of checkbox indices to update, or 'all' to update all
     * @param {boolean} checked The checked state to set
     * @param {HTMLUListElement|null} listElement The ul element to update. If null, uses the main list
     * @returns {number} Number of checkboxes updated
     * @example
     * // Check the first and third checkboxes
     * dataList.setCheckboxStates([0, 2], true)
     * // Uncheck all checkboxes
     * dataList.setCheckboxStates('all', false)
     */
    setCheckboxStates(indices, checked, listElement = null) {
        const targetList = listElement || this.#list

        if (!targetList) {
            console.warn(`[${this.localName}] ${this.id} setCheckboxStates: No list element found`)
            return 0
        }

        const checkboxes = targetList.querySelectorAll('input[type="checkbox"]')
        let updatedCount = 0

        if (indices === 'all') {
            checkboxes.forEach((checkbox) => {
                /** @type {HTMLInputElement} */ (checkbox).checked = checked
                updatedCount++
            })
        } else if (Array.isArray(indices)) {
            indices.forEach((index) => {
                if (index >= 0 && index < checkboxes.length) {
                    /** @type {HTMLInputElement} */ (checkboxes[index]).checked = checked
                    updatedCount++
                }
            })
        }

        // Issue custom event to notify checkbox states have been updated
        this._event('checkboxStatesUpdated', {
            list: targetList,
            updatedCount: updatedCount,
            checked: checked,
            indices: indices,
        })

        return updatedCount
    }

    // #region ---- Private methods ---- //

    /** Builds a list from input data
     * @param {Array|object} listData Source data for the list, can be an array or an object
     * @param {HTMLElement|ShadowRoot} parentEl The parent element to append the list to
     * @param {"ol"|"ul"} type  The type of list to create, either 'ol' for ordered or 'ul' for unordered
     * @param {number} depth  Recursion depth. Defaults to 1. Used to limit recursion depth for nested objects/arrays.
     * @private
     */
    _buildList(listData, parentEl, type, depth) {
        if (depth === undefined) depth = 1
        // console.log(`[${this.localName}] ${this.id} buildList: listData=${listData}, listEl=${parentEl}, depth=${depth}`)

        // Create a new list element (all lists are created as ul, numbering is done via CSS)
        const listEl = document.createElement('ul')
        if (depth > 1) {
            // If this is a nested list, add a class to the list element
            listEl.classList.add('nested-list')
            // and add a data-depth attribute to the list element
            listEl.setAttribute('data-depth', depth.toString())
        }

        Object.entries(listData).forEach(([key, value], i) => {
            // Is the key a number? Keep a record
            const keyIsNumeric = !isNaN(parseInt(key))

            // Is the value an object or array?
            if (typeof value === 'object' && value !== null) {
                if (depth > 3) {
                    // If depth is too high, just show the key and value as a string
                    listEl.insertAdjacentHTML(
                        'beforeend',
                        this._buildLIhtml({ arrayType: Array.isArray(listData), key, value: JSON.stringify(value), i, })
                    )
                    console.warn(`[${this.localName}] ${this.id} buildList: Depth limit reached for key "${key}", showing as string.`)
                    return
                }

                // Create a new list item for the key
                const li = document.createElement('li')

                // If key is not numeric and if this.keyvalueseparator is not 'NULL', add key as text
                if (!keyIsNumeric && this.keyvalueseparator !== 'NULL') {
                    const keySpan = document.createElement('span')
                    keySpan.textContent = `${key}${this.keyvalueseparator}`
                    // keySpan.classList.add('nested-key')
                    li.appendChild(keySpan)
                } else {
                    li.classList.add('nested-container')
                }

                // recurse to create a nested list
                this._buildList(value, li, type, ++depth)
                this._updateListAttributes(li, type)
                // add the new list item to its parent element
                listEl.appendChild(li)
            } else {
                // Otherwise, just create a list item with the key and value
                // console.log(`[${this.localName}] ${this.id} buildList: key=${key}, value=${value}, i=${i}`)
                listEl.insertAdjacentHTML(
                    'beforeend',
                    this._buildLIhtml({ arrayType: Array.isArray(listData), key, value, i, })
                )
            }
        })

        // Attach the list as the last child of the parent element
        parentEl.appendChild(listEl)
    }

    /** Builds a list item HTML string based on the provided options.
     * @param {object} options Object containing options for building the list item
     * @param {boolean} options.arrayType True if the list is an array, false if it is an object
     * @param {string} options.key The key for the list item, used as the id and data-index
     * @param {string} options.value The value for the list item, displayed as the text
     * @param {number} options.i The index of the item in the list, used for data-index attribute
     * @returns {string} The HTML string for the list item
     * @private
     */
    _buildLIhtml({ arrayType, key, value, i, }) {
        // check if this.#entries is an array
        if (arrayType) {
            return `<li id="${this.id}-${key}" data-index="${i}">${value}</li>`
        }
        // If the keyvalueseparator is 'NULL', then don't show the key
        // otherwise, show the key followed by the separator
        return `<li id="${this.id}-${key}" data-index="${i}">${this.keyvalueseparator === 'NULL' ? '' : `${key}${this.keyvalueseparator}`}${value}</li>`
    }

    /** Updates the list attributes based on the current type and style
     * @param {HTMLElement} listEl The list element to update
     * @param {"ol"|"ul"} listType The type of list to use, either 'ol' for ordered or 'ul' for unordered
     * @private
     */
    _updateListAttributes(listEl, listType) {
        if (listEl) {
            if (listEl) {
                // Update CSS classes/attributes instead of recreating element
                listEl.setAttribute('data-list-type', listType)

                // Update ARIA attributes for accessibility
                if (listType === 'ol') {
                    this.listStyle = 'decimal'
                    listEl.setAttribute('aria-label', 'Ordered list')
                } else {
                    this.listStyle = 'disc'
                    listEl.setAttribute('aria-label', 'Unordered list')
                }
            }
            if (this.#listStyle) {
                // Update the CSS variable for the list style
                listEl.style.setProperty('--list-style', this.#listStyle)
            }
        }
    }

    // #endregion ---- Private methods ---- //
} // ---- end of Class ---- //

// Make the class the default export so it can be used elsewhere
export default DataList

/** Self register the class to global
 * Enables new data lists to be dynamically added via JS
 * and lets the static methods be called
 */
window['DataList'] = DataList

// Self-register the HTML tag
customElements.define('data-list', DataList)
